const open_rankColumns = (t, object, t_month) => [
  {
    title: t('rank'),
    dataIndex: 'rank',
    width: '5%',
    render: Trophy,
    align: 'center',
    fixed: 'left',
  },
  ...(object == 'actor'
    ? [
        {
          title: t('avatar'),
          dataIndex: 'id',
          width: '5%',
          align: 'center',
          render: MyAvatar,
          fixed: 'left',
        },
      ]
    : []),
  {
    title: t(object),
    dataIndex: 'name',
    width: '20%',
    align: 'center',
    render: function (text) {
      if (object !== 'company') {
        return (
          <a
            href={'https://github.com/' + text}
            target="_blank"
            rel="noreferrer"
          >
            {text}
          </a>
        );
      } else {
        return text;
      }
    },
  },
  {
    title: t('insight_board'),
    dataIndex: 'name',
    align: 'center',
    width: '10%',
    render: function (text, row, index) {
      return dashboard(text, index, t_month, object);
    },
  },
  {
    title: t('influence'),
    dataIndex: 'value',
    width: '20%',
    align: 'right',
    render: (text, row, index) => {
      return RoundFloat(text);
    },
  },
  {
    title: '',
    dataIndex: 'valueDelta',
    width: '10%',
    align: 'left',
    render: (text, row, index) => {
      text = RoundFloat(text);
      return PointRender(text, row, index);
    },
  },
];
const solveDate = (year, month) => {
  if (year === null && month === null) {
    return 'not found';
  }
  if (month === null) {
    return year + '年';
  }
  return year + '年' + (month + 1) + '月';
};

function dashboard(text, index, t_month, object) {
  if (text.includes('huggingface')) {
    let [org_name, repo_name] = text.split('/');
    const t_month_copy = t_month + ' ' + '00:00:00';
    let params = {
      org_name,
      repo_name,
      t_month_copy,
      t_month,
    };
    return (
      <a
        href={
            text
        }
        target="_blank"
        rel="noreferrer"
      >
        <img
          alt="dashboard"
          src={process.env.PUBLIC_URL + "/pics/dashboard.png"}
          style={{ height: '20px', width: '20px' }}
        />
      </a>
    );
  } else {
    let params = {
      communityName: text,
    };
    let jsonString = JSON.stringify(params);
    return (
      <a
        href={
            text
        }
        target="_blank"
        rel="noreferrer"
      >
        <img
          alt="dashboard"
          src={process.env.PUBLIC_URL + "/pics/dashboard1.png"}
          style={{ height: '20px', width: '20px' }}
        />
      </a>
    );
  }
}

function DateTitle(props) {
  return <h1>{solveDate(props.year, props.month)}</h1>;
}

function MyTable(props) {
  const [loading, setLoading] = useState(true);
  const [dataSource, setDataSource] = useState([]);
  const [showSize, setShowSize] = useState(25);
  const { t } = useTranslation();
  
  useEffect(() => {
    console.log('[表格组件] 组件已挂载，使用硬编码数据');
    
    // 使用硬编码数据，不再尝试从CSV加载
    setDataSource(hardcodedData);
    setLoading(false);
    console.log(`[表格组件] 数据加载成功，共 ${hardcodedData.length} 条记录`);
    
  }, []);

  const expandData = () => {
    setShowSize(prevSize => prevSize + 25);
  };

  return (
    <div className="table">
      <div className="table-content">
        <Card
          style={{
            width: '100%',
            background: '#FFFFFF',
            boxShadow: '0px 15px 20px 15px #F7F7FF',
          }}
        >
          <Table
            columns={ssaColumns(t)}
            dataSource={dataSource.slice(0, Math.min(showSize, dataSource.length))}
            rowKey={(record) => record.model}
            pagination={false}
            loading={loading}
            scroll={{ x: 'max-content' }}
            rowClassName={(record, index) => (index % 2 === 0 ? 'table-even' : 'table-odd')}
          />
          
          <Row style={{ marginTop: '10px' }}>
            <Col span={12}>
              <Row justify="start">
                <Col>
                  {showSize < dataSource.length ? (
                    <a
                      style={{
                        color: '#FFCC19',
                        fontSize: '18px',
                      }}
                      onClick={expandData}
                    >
                      {t('showMore') + '>>'}
                    </a>
                  ) : (
                    <span
                      style={{
                        color: 'gray',
                        fontSize: '18px',
                      }}
                    >
                      {t('noMore')}
                    </span>
                  )}
                </Col>
              </Row>
            </Col>
            <Col span={12}>
              <Row justify="end">
                <Col>
                  <QAmiss />
                </Col>
              </Row>
            </Col>
          </Row>
        </Card>
      </div>
    </div>
  );
}

export default MyTable;
